.experiences-section {
  width: 100%;
  max-width: 900px;
  margin: 0 auto 60px auto;
  padding: 0 0 40px 0;
  position: relative;
  z-index: 1;
}

.timeline-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2.5rem;
  color: #28262c;
  text-align: left;
}

.timeline-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  position: relative;
  min-height: 400px;
}

.timeline-bar {
  width: 6px;
  background: linear-gradient(180deg, #73937e 0%, #b0c9a8 100%);
  border-radius: 4px;
  height: 100%;
  min-height: 350px;
  position: absolute;
  left: 20px;
  top: 0;
  transform-origin: top;
  z-index: 0;
}

.timeline-items {
  flex: 1;
  margin-left: 60px;
  display: flex;
  flex-direction: column;
  gap: 60px;
}

.timeline-item {
  display: flex;
  align-items: flex-start;
  gap: 24px;
  position: relative;
  cursor: pointer;
  z-index: 2;
}

.timeline-dot {
  width: 18px;
  height: 18px;
  background: #73937e;
  border-radius: 50%;
  border: 4px solid #f1f7f1;
  position: absolute;
  left: -42px;
  top: 8px;
  box-shadow: 0 2px 10px rgba(115,147,126,0.18);
  z-index: 3;
  transition: background 0.3s;
}

.timeline-item:hover .timeline-dot {
  background: #b0c9a8;
}

.timeline-content {
  background: rgba(40, 38, 44, 0.04);
  border-radius: 10px;
  padding: 1.5rem 2rem;
  box-shadow: 0 4px 16px rgba(40, 38, 44, 0.08);
  min-width: 220px;
  max-width: 520px;
  transition: box-shadow 0.3s, background 0.3s;
}

.timeline-year {
  font-size: 1.35rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #73937e;
}

.timeline-short {
  font-size: 1.12rem;
  color: #28262c;
  font-weight: 500;
}

.timeline-long {
  font-size: 1.1rem;
  color: #28262c;
  font-weight: 400;
  margin-top: 0.2rem;
  line-height: 1.6;
}

@media (max-width: 700px) {
  .timeline-container {
    flex-direction: column;
  }
  .timeline-bar {
    left: 10px;
    min-height: 200px;
  }
  .timeline-items {
    margin-left: 38px;
    gap: 38px;
  }
  .timeline-content {
    padding: 1rem 1.2rem;
    min-width: 120px;
    max-width: 98vw;
  }
}
